*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: sans-serif;
}

body{
    background-color: #eff3ff;
}

#card {
    background: radial-gradient(circle at 50% 0%, 40%, #fff 36%);
    width: 100%;
    padding: 30px 20px;
    box-shadow: 0 20px 30px rgba(0, 0, 0, 0.15);
    border-radius: 10px;
    width: 350px;
    margin: 50px;
  }

  #card:hover{
    transform: scale(110%);
}

#fondo {
    border-radius: 50%;
    height: fit-content;
    width: fit-content;
    margin: 25%;
    transform: scale(100%);
}

  #card img {
    display: block;
    width: 180px;
    max-height: 200px;
    position: relative;
    margin: 20px auto;
    transform: scale(110%);
  }
  .top{
    display: flex;
  }

  .number{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 30px;
    height: 30px;
    background-color: #ffffff;
    border-radius: 50%;
    margin-right: auto;
    font-weight: 800;
  }

  .hp {
    width: 80px;
    background-color: #ffffff;
    text-align: center;
    padding: 8px 0;
    border-radius: 30px;
    margin-left: auto;
    font-weight: 600;
  }

  .poke-name {
    text-align: center;
    font-weight: 600;
  }

  .types {
    display: flex;
    justify-content: space-around;
    margin: 20px 0 40px 0;
  }

  .hp span,
  .types span {
    font-size: 12px;
    letter-spacing: 0.4px;
    font-weight: 600;
  }

  .types span {
    padding: 5px 20px;
    border-radius: 20px;
    color: #ffffff; 
  }

  .stats {
    display: flex;
    align-items: center;
    justify-content: space-between;
    text-align: center;
  }

  .stats p {
    color: #404060;
  }